body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  min-width: 1000px;
}

.TestField {
  padding: 16px;
}

.MiddleWrapper {
  text-align: center;
}

.PopConfirm {
  max-width: 160px;
}

.LoadDataDialog {
  height: 100%;
}

.LoadDataDialogInner {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.LoadDataDialogSubmit {
  margin-top: auto;
  width: 100%;
  border-top: 1px solid #e9e9e9;
  padding: 10px 16px;
  text-align: right;
}

.ant-drawer-wrapper-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.AutoInputNumber {
  width: 100%;
}

.ChartPresentation {
  padding: 0px 24px;
}

.ResetFormButton {
  margin-right: 16px;
}

.ant-empty {
  padding: 40px 0px;
}

.ChartSetup .ant-collapse-header {
  font-size: 1rem;
}

.LoadData .ant-row-flex + .ant-row-flex {
  padding-top: 8px;
}

.ConfigChartSection {
  padding: 8px 0;
  margin-top: 16px;
  font-weight: bold;
  background: linear-gradient(to right, #999 0%, #999 50%, #fff 50%, #fff 100%);
  background-position: top right;
  background-repeat: repeat-x;
  background-size: 10px 1px;
}



.ColorSelectColor {
  display: inline-block;
  vertical-align: text-bottom;
  height: 18px;
  width: 18px;
  border-radius: 50%;
  margin-right: 4px;
}

.Visible {
  display: unset;
}

.Hidden {
  display: none;
}

.CustomLabel {
  text-align: center;
  height: 100%;
  width: 100%;
  font-size: 1rem;
}

.CustomLabelInner {
  position: absolute;
  top: 50%;
  left: 50%;
}

.CustomLabelRotate {
  -webkit-transform: translateX(-50%) translateY(-50%) rotate(-90.1deg);
          transform: translateX(-50%) translateY(-50%) rotate(-90.1deg);
  white-space: nowrap;
}

.CustomLabelTransform {
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  white-space: nowrap;
}

.ChartTitle {
  text-align: center;
  font-size: 1.5rem;
  padding-bottom: 4px;
}

text {
  text-rendering: geometricPrecision;
}

.LineChart {
  position: relative;
  width: 100%;
}

.InnerChart {
  margin: auto;
}

.CustomTooltip {
  background: white;
  border: solid 1px #d4d4d4;
  border-radius: 4px;
  padding: 8px 8px 16px 8px;
  min-width: 120px;
  display: table;
}

.CTTime {
  font-weight: bold;
}

.CTProperty {
  display: table-row;
}

.CTFeature {
  display: table-cell;
  padding-right: 8px;
}

.CTValue {
  display: table-cell;
  text-align: right;
}

.RegresionSummary {
  padding: 0px 56px;
  font-size: 0.9rem;
}

.RegresionSummary .Title {
  font-size: 1rem;
  font-weight: bold;
}

.RegresionValue {
  display: table;
  padding-top: 16px;
}

